<template>
  <div>

    <router-view>
    </router-view>

  </div>
</template>

<script>
import Navbar from '../Navbar'

export default {

  name: 'Member',
  computed: {
    user() {
      return this.$store.state.user
    },

  },
  methods: {
    logout() {
      this.axios
        .delete(process.env.VUE_APP_API + '/users/logout')
        .then((res) => {
          // 
          if (res.data.success) {
            this.$swal({
              icon: 'success',
              title: '',
              text: '',
            })

            //  vuex
            this.$store.commit('logout')

            // 
            if (this.$route.path !== '/') {
              this.$router.push('/')
            }
          } else {
            this.$swal({
              icon: 'error',
              title: '',
              text: res.data.message,
            })
          }
        })
        .catch((error) => {
          //  200 alert 
          this.$swal({
            icon: 'error',
            title: '',
            text: error.response.data.message,
          })
        })
    },
    heartbeat() {
      this.axios
        .get(process.env.VUE_APP_API + '/users/heartbeat')
        .then((res) => {
          //  vuex 
          if (this.user.id.length > 0) {
            // 
            if (!res.data) {
              this.$swal({
                icon: 'error',
                title: '',
                text: '',
              })
              // 
              this.$store.commit('logout')
              // 
              if (this.$route.path !== '/') {
                this.$router.push('/')
              }
            }
          }
        })
        .catch(() => {
          this.$swal({
            icon: 'error',
            title: '',
            text: '',
          })
          // 
          this.$store.commit('logout')
          // 
          if (this.$route.path !== '/') {
            this.$router.push('/')
          }
        })
    },
  },

}
</script>
